@function em($size, $context: 14) {
  @return ($size/$context) * 1em; }

@function rem($size, $context: 10) {
  @return ($size/$context) * 1rem; }

@function img-url($image) {
  @return url('../img/#{$image}'); }

@mixin image($image) {
  background-image: img-url($image); }

@mixin trans($what: all, $dur: 0.2s, $easing: ease) {
  transition: $what $dur $easing; }

@mixin smooth-scroll {
  -webkit-overflow-scrolling: touch; }

@mixin smooth-font {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

@mixin f($name, $font-size: null, $letter-spacing: null) {
  $font-family: null;
  @if $name == "l" {
    $font-family: "light"; }
  font-family: $font-family;
  font-weight: normal;
  @if $font-size != null {
    font-size: $font-size + px; }
  @if $letter-spacing != null {
    letter-spacing: ($letter-spacing / 1000) + em; } }

@mixin gpu {
  transform: translate3d(0,0,0); }

@mixin md {
  @include r(1023) {
    @content; } }

@mixin sm {
  @include r(767) {
    @content; } }

@mixin xs {
  @include r(599) {
    @content; } }

@mixin responsiveImg($name, $width, $height, $type:'png') {
  $image: '#{$name}.#{$type}';
  $imageRetina: '#{$name}@2x.#{$type}';
  width: 100%;
  max-width: em($width);
  background: img-url($image) no-repeat center;
  background-size: cover;
  &:before {
    content: "";
    display: block;
    padding-bottom: percentage($height / $width); }
  @include retina {
    background-image: img-url($imageRetina); }
  @include rmin(1400) {
    background-image: img-url($imageRetina); } }

@mixin respond-to($media) {
  @if $media == desktop-large {
    @media only screen and (min-width: 1440px) {
      @content; } }
  @else if $media == desktop {
    @media only screen and (min-width: 1024px) and (max-width: 1440px) {
      @content; } }
  @else if $media == mobile-large {
    @media only screen and (min-width: 440px) and (max-width: 1024px) {
      @content; } }
  @else if $media == mobile {
    @media only screen and (max-width: 439px) {
      @content; } } }


