.clearfix {
    @include clr;
}

.content-wrap {
    position: relative;
    max-width: 100%;
    padding-left: $page-padding-left;
    padding-right: $page-padding-right;
    box-sizing: border-box;
    margin: 0 auto;
    @include respond-to(desktop) {
        padding-left: $page-padding-left_desktop;
        padding-right: $page-padding-right_desktop;
    }
}

.head__logo-tagline {
    $showing-time: 3s;
    .logo {
        //float: left;
        //width: 180px;

        @include f(m-semibold);
        font-size: 15px; //20px;
        line-height: 150%;
        letter-spacing: 6px;
        text-transform: uppercase;
        color: #333333;
        margin-bottom: 9px;

        //opacity: 0;
        //visibility: hidden;
        transition: opacity $showing-time,
        visibility $showing-time,
        color .5s;
        @include respond-to(desktop) {
            //width: 120px;
            margin-bottom: 6px;
            font-size: 10px; //14px;
            letter-spacing: 4px;
        }
        &.logo_big {
            display: block;
        }
        &.logo_small {
            display: none;
        }
        .dark &, .log-in-state & {
            color: white;
        }
    }
    .tagline {
        //float: left;
        //width: 165px;

        @include f(p-regular);
        font-size: 18px; //20px;
        line-height: 130%;
        color: #AAAAAA;

        opacity: 0;
        visibility: hidden;
        transition: opacity $showing-time, visibility $showing-time;
        transition-delay: $showing-time * .25;
        @include respond-to(desktop) {
            //width: 110px;
            font-size: 12px;
        }
        .log-in-state & {
            opacity: 0;
            visibility: hidden;
            transition: opacity .25s, visibility .25s;
            transition-delay: 0s;
        }
    }
    .mail-n-password {
        position: relative;
        float: left;
        width: 850px;
        @include respond-to(desktop) {
            width: 566px;
        }
    }
    .close-btn {
        right: 0;
        @include respond-to(desktop) {
            right: 0;
        }
    }
}

.log-in {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1004;

    background-color: #333333;
    transition: transform .75s;
    .head__logo-tagline {
        position: relative;
        margin-top: 66px;
        margin-bottom: 80px;
        @include respond-to(desktop) {
            margin-top: 44px;
            margin-bottom: 50px;
        }
        .logo {
            width: 440px;
            float: left;
            @include respond-to(desktop) {
                width: 290px;
            }
        }
        .mail-n-password {
            height: 75px;
            @include respond-to(desktop) {
                height: 50px;
            }
            .input-field {
                position: absolute;
                top: 0;
                width: 47%;
            }
            .input-field_email {
                left: 0;
            }
            .input-field_password {
                right: 0;
            }
            .helper-btn {
                position: absolute;
                left: 53%;
                top: 100%;
                @include f(p-regular);
                font-size: 12px;
                letter-spacing: 6px;
                text-transform: uppercase;
                color: white;
                cursor: pointer;
                transform: translateY(-18px);
                transition: left .35s;
                @include respond-to(desktop) {
                    font-size: 8px;
                    letter-spacing: 4px;
                    transform: translateY(-8px);
                }
                .helper-btn__content {
                    position: absolute;
                    left: 0;
                    top: 0;
                    white-space: nowrap;
                    transition: visibility .35s,
                    opacity .35s;
                    &.hidden {
                        visibility: hidden;
                        opacity: 0;
                    }
                }
                .helper-btn__content_back {
                    padding-left: 75px;
                    @include respond-to(desktop) {
                        padding-left: 50px;
                    }
                    &:before {
                        content: '';
                        display: block;
                        position: absolute;
                        left: 0;
                        top: 40%;
                        width: 50px;
                        height: 2px;
                        background-color: #AAAAAA;
                        transform: translateY(-50%);
                        @include respond-to(desktop) {
                            width: 33px;
                        }
                    }
                    &:after {
                        content: '';
                        display: block;
                        position: absolute;
                        left: 0;
                        top: 40%;
                        width: 6px;
                        height: 6px;
                        border-left: 2px solid #AAAAAA;
                        border-bottom: 2px solid #AAAAAA;
                        transform: translateY(-50%) rotate(45deg);
                        @include respond-to(desktop) {
                            width: 4px;
                            height: 4px;
                        }
                    }
                }
            }
            &.recover-pswd-state {
                .input-field_email {
                    width: 100%;
                }
                .input-field_password {
                    visibility: hidden;
                    opacity: 0;
                }
                .helper-btn {
                    left: 0;
                }
            }
        }
    }
    .sub-header {
        @include f(p-regular);
        font-size: 21px;
        line-height: 120%;
        color: white;
        margin-bottom: 180px;
        max-width: 1440px;
        @include respond-to(desktop) {
            font-size: 14px;
            margin-bottom: 120px;
            max-width: 950px;
        }
    }
    &.hidden {
        transform: translateY(-100%);
    }
}

