.hub-link {
  display: block;
  position: relative;
  color: inherit;
  text-decoration: none;
  > .h1-like {
    display: block;
    vertical-align: top;
    margin-bottom: rem(10);
    max-width: rem(500); }
  .subtitle {
    display: inline;
    background: white;
    box-shadow: rem(25) 0 0 0 white;
    transition: color 1s ease-out; }
  &.is-ready {
    > .h1-like {
      visibility: hidden; } } }

.hub-link__line {
  position: absolute;
  bottom: 0.5em;
  right: 0;
  left: 0;
  @include f(m-regular, 10, 10, 250);
  text-transform: uppercase;
  text-align: right;
  padding-right: rem(130);
  &:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 50%;
    right: 0;
    left: 0;
    border-top: 1px solid $color-line; }
  &:after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    width: rem(6);
    height: rem(6);
    border: rem(1) solid;
    border-color: $color-line $color-line transparent transparent;
    transform-origin: center center;
    transform: translateY(-45%) rotate(45deg); } }
