.video-container {
  position: relative;
  padding: 30px 0 0;
  background: $color-text-main no-repeat center;
  background-size: cover;
  color: white;
  height: rem(400);
  overflow: hidden;
  box-sizing: border-box;
  > iframe {
    position: relative;
    z-index: 0;
    cursor: none; }
  .video-poster {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: no-repeat center;
    background-size: cover;
    transition: transform 0.5s ease; }
  //.video-cursor
  //  padding: 1px 8px 1px 22px
  //  +f(m-bold, 10, 10, 250)
  //  background: white
  //  z-index: 2
  //  color: $color-text-main
  //  text-transform: uppercase
  //  &.is-play
  //    &:before
  //      content: ""
  //      position: absolute
  //      top: 50%
  //      left: 8px
  //      +arr(6,8,currentColor,r)
  //      margin-top: -4px
  &.is-ready {
    cursor: img-url('cursors/play.png'), pointer;
    &:hover:not(.is-active) {
      .video-poster {
        transform: scale(1.1); } } }
  &.is-active {
    background: black;
    cursor: auto;
    .video-container__content {
      transform: translate(-100%, -50%);
      opacity: 0;
      visibility: hidden; } }
  &.is-opened {
    .video-container__close {
      opacity: 1;
      visibility: visible; } } }

.video-container__content {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: $inner-padding + $outer-padding;
  transform: translate(0, -50%);
  margin-top: -0.5px;
  transition: all 0.5s ease; }

.video-container__close {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  z-index: 2;
  top: 10px;
  right: 10px; }
